<template>
  <h1 class="title">Методы (компонент Methods)</h1>
  <p class="text">
    Сверстать три кнопки с текстами: DD.MM.YYYY, YYYY-MM-DD, DD/MM/YYYY. При нажатии на соответствующую кнопку
    выводить сегодняшнюю дату в формате, описанном в тексте кнопок.
  </p>
  <button @click="setDateOne" class="btn">{{dateOne ? dateOne : 'DD.MM.YYYY'}}</button>
  <button @click="setDateTwo" class="btn">{{dateTwo ? dateTwo : 'YYYY-MM-DD'}}</button>
  <button @click="setDateThree" class="btn">{{dateThree ? dateThree : 'DD/MM/YYYY'}}</button>
</template>

<script>
import {ref} from 'vue'
export default {
  name: "ThisMethods",
  setup () {
    const dateOne = ref('')
    const dateTwo = ref('')
    const dateThree = ref('')
    const day = ref(new Date().getDate() > 9 ? new Date().getDate() : '0' + new Date().getDate())
    const month = ref(new Date().getMonth() + 1 > 9 ? new Date().getMonth() + 1 : '0' +  (new Date().getMonth() + 1))
    const year = ref(String(new Date().getFullYear()))

    const setDateOne = () => {
      dateOne.value = `${day.value}.${month.value}.${year.value}`
    }

    const setDateTwo = () => {
      dateTwo.value = `${year.value}-${month.value}-${day.value}`
    }

    const setDateThree = () => {
      dateThree.value = `${day.value}/${month.value}/${year.value}`
    }

    return {
      dateOne,
      dateTwo,
      dateThree,
      setDateOne,
      setDateTwo,
      setDateThree
    }
  }
}
</script>

