<template>
  <h1 class=" title">Связываем данные с шаблоном (компонент DateToTemplate)</h1>
  <p class=" text">
    Сверстать инпут и кнопку. При нажатии на кнопку введенный текст отобразить ниже инпута, значение инпута очистить.
  </p>
  <input v-model="inputValue" class="input" type="text">
  <button @click="onClick" class="btn">ОК</button>
  <p>{{text}}</p>
</template>

<script>
import {ref} from 'vue'
export default {
  name: "DateToTemplate",
  setup () {
    const inputValue = ref('')
    const text = ref('')

    const onClick = () => {
      text.value = inputValue.value
      inputValue.value = ''
    }

    return {
      inputValue,
      text,
      onClick
    }
  }
}
</script>

