<template>
  <h1 class="title">Модификаторы событий (компонент CustomDirective)</h1>
  <p class="text">
    Написать директиву, по аналогии с библиотекой  v-click-outside
  </p>
  <button @click="toggleModal" class="btn">Открыть модалку</button>
  <div v-if="isShowModal" v-click-outside="toggleModal" class="modal">
    <h3 class="modal__title">Модалка с кастомной директивой</h3>
    <h4 class="modal__title">Модалка с кастомной директивой</h4>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  directives: {
    'click-outside': {
      mounted (el,value, vnode) {
        setTimeout(() => {
          document.addEventListener('click', function handler (e) {

            e.stopPropagation()

            let children = vnode.children.map(item => {
              return item.el
            })

            if(!([...children, el].includes(e.target))) {
              value.value()
              this.removeEventListener('click', handler);
            }
          })
        },)
      },
    }
  },
  setup () {
    const isShowModal = ref(false)

    const toggleModal = () => {
      isShowModal.value = !isShowModal.value
    }

    return {
      isShowModal,
      toggleModal
    }
  }
}
</script>

<style scoped>
.modal {
  position: fixed;
  left: 50%;
  top: 30%;
  transform: translate(-50%,-50%);
  background-color: #ccc;
  box-shadow: 10px 5px 5px #000;
  padding: 10px;
}

</style>
