<template>
  <h1 class="title">Вычисляемые свойства (компонент ComputedProperty)</h1>
  <p class="text">
    сверстать два инпута и привязать к ним соответствующие реактивные переменные
    вывести ниже блок с суммой, которая расcчитывается с помощью computed
  </p>
  <input class="input" type="number" v-model.number="inputValueOne">
  <input class="input" type="number" v-model.number="inputValueTwo">
  <p class="text">Сумма: {{sum}}</p>
</template>

<script>
import {ref, computed} from 'vue'
export default {
  name: "ComputedProperty",
  setup() {
    const inputValueOne = ref(null)
    const inputValueTwo = ref(null)
    const sum = computed(() => inputValueOne.value + inputValueTwo.value)

    return {
      inputValueOne,
      inputValueTwo,
      sum
    }
  }
}
</script>

