<template>
  <h1 class="title">Условная отрисовка (компонент ConditionalRendering)</h1>
  <p class="text">
    Сверстать кнопку.
    Добавить реактивную переменную counter(начальное значение = 0)
    При нажатии на кнопку увеличивать значение счетчика.
    В шаблоне выводить:
    Если счетчик равен 0 - выводить слово “ноль”
    Если больше нуля и меньше 10 - выводить слово “меньше 10”
    Если больше или равно 10 - “больше 10”
  </p>
  <p class="text" v-if="count === 0">Счетчик равен: ноль</p>
  <p class="text" v-if="count > 0 && count < 10">Счетчик равен: меньше 10</p>
  <p class="text" v-if="count >= 10">Счетчик равен: больше 10</p>
  <button @click="incrCount" class="btn">Увеличить счетчик</button>
</template>

<script>
import {ref} from "vue";

export default {
  name: "ConditionalRendering",
  setup () {
    const count = ref(0)

    const incrCount = () => {
      count.value++
    }

    return {
      count,
      incrCount
    }
  }
}
</script>

