<template>
  <div class="counter">
    <h1 class="counter__title title">Начало работы с Composition API (компонент StartCompositionApi)</h1>
    <p class="counter__descr text">
      Создать два счетчика, объявить один через ref, второй через reactive объект с свойством счетчика
      Сверстать 2 кнопки, значение счетчиков вывести в тексты соответствующих кнопок. При нажатии увеличивать на 1 нужный счетчик
    </p>
    <button @click="incrCountOne" class="counters__one btn">Счетчик ref: {{counterOne}}</button>
    <button @click="incrCountTwo" class="counters__two btn">Счетчик reactive: {{counterTwo.count}}</button>
  </div>


</template>

<script>

import {ref, reactive} from "vue";

export default {
  name: "StartCompositionApi",

  setup() {
    const counterOne = ref(0)
    const counterTwo = reactive({
      count: 0
    })

    const incrCountOne = () => {
      counterOne.value++
    }
    const incrCountTwo = () => {
      counterTwo.count++
    }

    return {
      counterOne,
      incrCountOne,
      counterTwo,
      incrCountTwo
    }
  }
}
</script>

<style scoped>

.counter {
  /*display: flex;*/
