import { createRouter, createWebHistory } from 'vue-router'
import StartCompositionApi from "@/components/StartCompositionApi";
import DataToTemplate from "@/components/DataToTemplate";
import Methods from "@/components/Methods";
import ConditionalRendering from "@/components/ConditionalRendering";
import Cycles from "@/components/Cycles";
import ComputedProperty from "@/components/ComputedProperty";
import WatchChanges from "@/components/WatchChanges";
import EventModeifiers from "@/components/EventModeifiers";
import CustomDirective from "@/components/CustomDirective";

const routes = [
  {
    path: '/composition',
    component: StartCompositionApi
  },
  {
    path: '/template',
    component: DataToTemplate
  },
  {
    path: '/methods',
    component: Methods
  },
  {
    path: '/rendering',
    component: ConditionalRendering
  },
  {
    path: '/cycles',
    component: Cycles
  },
  {
    path: '/computed',
    component: ComputedProperty
  },
  {
    path: '/watch',
    component: WatchChanges
  },
  {
    path: '/modifiers',
    component: EventModeifiers
  },
  {
    path: '/directive',
    component: CustomDirective
  }

]

const router = createRouter({
  history: createWebHistory(),
  routes
});
export default router;
