<template>
  <h1 class="title">Модификаторы событий (компонент EventModeifiers)</h1>
  <p class="text">
    сверстать инпут, по клику на кнопку Enter выводить алерт с содержимым текстового поля
  </p>
  <input class="input" v-model="inputValue" @keydown.enter="onEnter" type="text">
</template>

<script>
import {ref} from "vue";
export default {
  name: "EventModeifiers",
  setup () {
    const inputValue = ref('')

    const onEnter = () => {
      alert(inputValue.value)
    }

    return {
      inputValue,
      onEnter
    }
  }
}
</script>

