<template>
  <h1 class="title">Циклы (компонент Cycles)</h1>
  <p class="text">
    Отрисовать массив из объектов с помощью циклов. <br>

    Вывести в шаблон имя, ключ = id
  </p>
  <p class="text"> [ <br>
    {id: 1, name: 'Alex'}, <br>
    {id: 2, name: 'Alex'}, <br>
    {id: 3, name: 'Alex'}, <br>
    {id: 4, name: 'Alex'}, <br>
    {id: 5, name: 'Alex'}, <br>
    {id: 6, name: 'Alex'}, <br>
    ]</p>
  <p class="text">Добавить кнопку рядом с именем с текстом "Удалить", при клике на кнопку удалять соответствующий элемент из массива.</p>
  <ul class="list">
    <li
        v-for="(item, index) in arr"
        :key="item.id"
        class="text list__item"
    >
      {{item.name + item.id}}
      <button @click="remove(index)" class="btn">Удалить</button>
    </li>
  </ul>
</template>

<script>
import {reactive} from "vue";

export default {
  name: "ThisCycles",

  setup () {
    const arr = reactive([
      {id: 1, name: 'Alex'},
      {id: 2, name: 'Alex'},
      {id: 3, name: 'Alex'},
      {id: 4, name: 'Alex'},
      {id: 5, name: 'Alex'},
      {id: 6, name: 'Alex'},
    ])

    const remove = (index) => {
      arr.splice(index,1)
    }

    return {
      arr,
      remove
    }
  }
}
</script>

