<template>
  <h1 class="title">Условная отрисовка (компонент WatchChanges)</h1>
  <p class="text">
   Дан массив
  </p>
  <p class="text">Вывести в цикле элементы массива и привязать значение title к пользовательскому вводу(input)</p>
  <p class="text">С посощью watch отслеживать изменения и подсвечивать строку, в котором призводится ввод текста</p>
  <ul class="list">
    <li
        v-for="item in data"
        :key="item.id"
        class="list__item"
    >
      <input class="input" :class="{border: item.change}" type="text" v-model="item.title" >
    </li>
  </ul>
</template>

<script>
import {reactive, watch} from "vue";

export default {
  name: "WatchChanges",
  setup () {
    const data = reactive([
      {id: 1, title: '', change: false},
      {id: 2, title: '', change: false},
      {id: 3, title: '', change: false}
    ])

    watch(() => ([...data[0].title]), () => {
      data[0].change = true
      data[1].change = false
      data[2].change = false
    })

    watch(() => ([...data[1].title]), () => {
      data[0].change = false
      data[1].change = true
      data[2].change = false
    })

    watch(() => ([...data[2].title]), () => {
      data[0].change = false
      data[1].change = false
      data[2].change = true
    })

    return {
      data
    }
  }
}
</script>

<style scoped>
.border {
  border: 4px solid red;
}
</style>
